@use "Base/variables" as v;
@use "Base/mixins" as m;

.sidebar{
    position: fixed;
    background-color:v.$secundario;
    z-index: 999;
    transition: v.$tran-bar;
    bottom: 0rem;
    width: 100%;
    height: 7rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 0.2rem solid rgb(101, 99, 101);
    //left: 25%;
    @include m.tablet{
        background-color: v.$secundario;
        border-radius: 0;
        padding: 2rem;
        flex-direction: column;
        justify-content: flex-start;
        top: 0;
        left: 0;
        height: 100%;
        width: 25rem;
    }
    .header{
        position: relative;
        flex-direction: row;
        display: none;
        @include m.tablet{
            flex-direction: column;
            display: flex;
        }
    }
    .sidebar__marca{
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 1rem;
        height: 10rem;
        @include m.tablet{
            width: 80%;
            margin-left: 2.4rem;

        }
    }
    .sidebar__imagen{
        width: 10rem;
        border-radius: 0.6rem;
        display: none;
        @include m.tablet{
            display: block;
        }
    }
    .sidebar__texto{
        text-align: center;
        transition: v.$tran-text;
        display: none;
        @include m.tablet{
            display: block;
            color: v.$blanco;
        }
    }
    .sidebar__texto-marca{
        font-size: 2rem;
        margin-bottom: 1rem;
        @include m.tablet{
            color: v.$blanco;
        }
    }
    .sidebar__texto-slogan{
        font-size: 1.2rem;
        font-family: v.$texto;
        @include m.tablet{
            color: v.$blanco;
        }
    }

    .sidebar__menu{
        height: 100%;
        position: absolute;
        padding: 0 2rem;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        margin: 0 auto;
        gap: 1.5rem;
        bottom: 0;
        @include m.tablet{
            width: 20rem;
            padding: 0;
            height: 80%;
            bottom: 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: 8%;
        }
    }
    .sidebar__menu-option{
        display: flex;
        align-items: center;
        gap: 2rem;
        width: 6rem;
        height: 6rem;
        padding: 0rem;
        border-radius: 2rem;
        cursor: pointer;
        transition: v.$tran-btn;
       
        @include m.tablet{
            width: 100%;
              
        }
        
        &:hover{
            .icon{
                 //transform: translateY(-3rem);
                transition: v.$tran-bar;
                    svg{
                        fill:v.$blanco;
                    } 
               
            }
            @include m.tablet{
                background-color: v.$blanco;
                .icon{
                    transform: none;
                    transition: none;

                    svg{
                        
                        fill: v.$negro;
                    }
                }
            }
        }
        //Opcion activa en ese momento
        &.option__active{
            .icon{
                 transform: translateY(-0.4rem);
                    svg{
                        fill:v.$blanco;
                    }
                @include m.tablet{
                    transform: none;
                  
                    
                }
            }
        }
        &:nth-child(1){
            &.option__active{
                &~.option__indicator{
                    transform: translateX(1rem * 0.49);
                }
            }
        }
        &:nth-child(2){
            &.option__active{
                &~.option__indicator{
                    transform: translateX(7.6rem * 1.08);
                }
            }
        }
        &:nth-child(3){
            &.option__active{
                &~.option__indicator{
                    transform: translateX(7.6rem * 2.16);
                }
            }
        }
        &:nth-child(4){
            &.option__active{
                &~.option__indicator{
                    transform: translateX(7.6rem * 3.138);
                }
            }
        }
        @include m.tablet{
            padding: 1rem 2rem;
        }
        .icon{
            width: 5rem;
            height: 70%;
            display: flex;
            justify-content: center;
            z-index: 999;
            
            @include m.tablet{
                width: 2rem;
                height: 2rem;
            }
        }
        &:hover{
            .option__text{
                color: v.$secundario;
            }
        }
    }

    .option__indicator{
        position: absolute;
        bottom: 21%;
        width: 5rem;
        left: 2rem;
        height: 5rem;
        background: v.$secundario;
        border-radius: 50%;
        border-bottom: 0.6rem solid v.$secundario-hover;
        // border: 0.1rem solid v.$blanco;
        transition: 0.5s;
        @include m.tablet{
            display: none;
        }
        &::before{
            content: '';
            position: absolute;
            top: 56%;
            left: -2.1rem;
            width: 2rem;
            height: 2rem;
            background-color: v.$negro;
            border-top-right-radius: 2rem;
            box-shadow: 0 -1rem 0 0 v.$blanco;
            opacity: 0;
        }
        &::after{
            content: '';
            position: absolute;
            top: 56%;
            right: -2.1rem;
            width: 2rem;
            height: 2rem;
            background-color: v.$negro;
            border-top-left-radius: 2rem;
            box-shadow: 0 -1rem 0 0 v.$blanco;
            opacity: 0;
        }
    }

    .option__text{
        // text-transform: uppercase;
        //noborrar 
        width: 6rem;
        font-family: v.$titulos;
        font-size: 2rem;
        transition: v.$tran-text;
        display: none;
        margin:0;
        @include m.tablet{
            color: v.$blanco;
            display: block;
        }
    }
    
    .toggle{
        position: absolute;
        top: 50%;
        right: 17rem;
        transform: translateX(21rem);
        
        height: 4rem;
        width: 4rem;
        background-color: v.$secundario;
        display: none;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        padding: 0.8rem;
        transition: v.$tran-bar;
        cursor: pointer;
        
        &:hover{
            background-color: v.$secundario-hover;
        }
        @include m.tablet{
            display: flex;
        }
    }

    .toggle svg{
        transition: v.$tran-bar;
        transform: rotate(180deg);
    }
    .optionLogo__image{
        position: absolute;
        height: 8rem;
        bottom: 2rem;
    }

    &.close{
        width: 100%;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        @include m.tablet{
            display: block;
            width: 7.8rem;
            padding-left: 1.5rem;
            left: 0;
        }
        .sidebar__menu{
            display: grid;
            grid-template-columns: repeat(4, 6rem);
            gap: 1.8rem;
            bottom: 0;
            
            @include m.tablet{
                display: flex;
                flex-direction: column;
                justify-content: center;
                bottom: 4rem;
                gap: 8%;
                width: 5rem;
            }
        }
        .sidebar__menu-option{
            gap: 0;
            display: flex;
            justify-content: center;
            padding-bottom: 0.51rem;
            @include m.tablet{
                padding: 1rem;
            }
        }
        .sidebar__texto{
            display: none;
        }
        .option__text{
            display: none;
        }
        .sidebar__marca{
            margin-left: 0;
            display: block;
            gap: 0;
            transition: v.$tran-bar;
        }
        .sidebar__imagen{
            width: 4rem;
            margin-top: 2.5rem;
        }
        .toggle{
            right: -3.2rem;
            // transform: none;
            width: 3rem;
            height: 3rem;
            transition: v.$tran-bar;
            transform: translateX(0.5rem);
        }

        .toggle svg{
            transition: v.$tran-bar;
            transform: rotate(0deg);

        }
    }
}

//Efecto blur
.blur{
    // background: rgba(255, 255, 255, 0.30);
    backdrop-filter: blur(2px);
    z-index: 4;
    width: 100%;
    height: 100%;
    position: fixed;
}

body{
    overflow-x: hidden;
}

//Ejemplo de contenido para ver mejor los cambios

.content{
    // background-color: red;
    height: 200rem;
    @include m.tablet{
        margin-left: 7.8rem;
    }
}

.sidebar__menu-option.option__active:hover .icon svg {

    @include m.tablet{
        fill: v.$negro; 
    }
    
}