@use 'Base/variables' as v;

//Fuente del ttulo
.container {
    .Titulo {
        font: v.$titulos;
        margin: 0;
        font-size: v.$titulos-size;
    }
}

/**********CALENDARIO**********/
//Funcionalidad del calendario
input[type="date"]::-webkit-inner-spin-button,
::-webkit-calendar-picker-indicator,
::-webkit-datetime-edit {
    display: none;
}
// Mostrar el calendario al hacer click
input[type="date"]::-webkit-calendar-picker-indicator {
    display: block;
    background: transparent;
    bottom: 0;
    color: transparent;
    cursor: pointer;
    height: auto;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: auto;
}
// Mostrar la fecha seleccionada al estar en hover 
input[type="date"]:hover::-webkit-datetime-edit {
    display: block;
}


//Centrar el botn
.Boton{
    display: flex;
    justify-content: center;
}
//Color del botn
.container{
    .Formulario{
        .Boton{
            .__boton{
                background-color:v.$secundario;
                color: v.$blanco;
                font-family: v.$texto;
                font-size: v.$texto-size;
                border:1rem;
                padding:1.2rem;
            }
        }
    }
}

//Hover del botn
.container{
    .Formulario{
        .Boton{
            .__boton:hover{
                background-color:v.$secundario-hover;
                color: v.$blanco-hover;
            }
        }
    }
}

