@use 'Base/variables' as v;
@use 'Base/mixins' as m;

    .Operacion{
            backdrop-filter: blur(10px) saturate(100%);
            -webkit-backdrop-filter: blur(10px) saturate(100%);
            background-color: rgba(0, 0, 0, 0.382);
            border-radius: 12px;
            border: 1px solid rgba(15, 13, 13, 0);
            width: 30rem;
            height: 15rem;
            padding:  0 1rem;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            position: fixed;
            z-index: 999;
            left: 30%;
            top: 25%;
        .Operacion__titulo{
            font: v.$subtitulos;
            font-size: v.$subtitulos-size - 6rem;
            text-align: center;
            margin-bottom: 1rem;
            // text-transform: uppercase;
            color: v.$blanco;
            font-weight: bold;
        }
        .Operacion__btn{
            font: v.$texto;
            margin: 0;
            padding: 1rem;
            width: 10rem;
            height: 6rem;
            border: none;
            background-color: v.$secundario;
            color: v.$blanco;
            font-weight: bold;
            font-size: 2rem;
            border: 1px solid v.$negro;
            border-radius: 1.2rem;
            &:hover{
                transition: all .3s;
                background-color: v.$primario-hover;
                color: v.$negro;
            }
        }
    }
    @include m.tablet{
        .Operacion{
            width: 50rem;
            height: 25rem;
            padding: 2rem;
        }
        .Operacion__titulo{
            font-size: v.$subtitulos-size;
            margin-bottom: 4rem;
        }
        .Operacion__btn{
            margin: 2rem 0;
            &:hover{
                font-size: 2.4rem;
            }
        }
    }

.ventanas__close{
    display: none !important;
}