@use 'Base/variables' as v;

//Hover de la tabla
.table-hover tbody tr:hover td,
.table-hover tbody tr:hover th {
    background-color: v.$table-hover;
}

//Diseo de botones
.dt-paging-button {
    padding: 0.5rem !important;
    border: none !important;
}

table tr th {
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

//Color de la cabecera de la tabla
.container{
    .__Tabla{
        .tabla{
            .tabla__cabecera{
                .cabecera{
                    background-color: v.$primario;
                }
            }
        }
    }
}
.container{
    .__Tabla{
        .tabla{
            .tabla__cabecera{
                .cabecera:hover{
                    background-color: v.$primario-hover;
                }
            }
        }
    }
}

//Botones acciones
.acciones {
    text-align: center;
}

.tabla .btn {
    display: inline-flex;
}

.tabla .btn img {
    width: 1.8rem;
    height: 1.8rem;
}

