@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.register{
    display: flex;
    flex-direction: column;
    margin-left: 0;
    height: 100%;
    @include m.tablet{
        flex-direction: row;
        height: 100vh;
        min-height: 76.8rem;
    }
    .registerHero{
        background-color: v.$primario;
        min-height: 30rem;
        @include m.tablet{
            display: flex;
            min-height: 0;
            width: 40%;
            flex-direction: column;
            justify-content: center;
        }
        .registerHero__titulo{
            color: v.$negro;
            font-weight: bold;
            text-transform: uppercase;
            text-align: center;
            margin: 3rem 0;
            font-size: 3.2rem;
            @include m.tablet{
                display: none;
            }
        }
        .registerHero__imagen{
            max-width: 16rem;
            margin: 0 auto;
        }
    }

    .registerForm{
        text-align: left;
        background-color: v.$blanco;
        padding: 2rem 4rem 0 4rem;
        @include m.tablet{
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 60%;
        }
        .registerForm__titulo{
            display: none;
            @include m.tablet{
                display: inline-block;
                margin-bottom: 1rem;
                font-size: 2.4rem;
            }
        }
        .registerForm__email{
            font-size: 1.6rem;
        }
        .inputForm {
            border: 1.5px solid #ecedec;
            border-radius: 10px;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-left: 10px;
            transition: 0.2s ease-in-out;
            margin-bottom: 2rem;
            font-size: 1.4rem;
            @include m.tablet{
                max-width: 50rem;
            }
            .view-icon{
                cursor: pointer;
                margin-right: 2rem;
            }
            .input {
              margin-left: 10px;
              border-radius: 10px;
              border: none;
              width: 85%;
              height: 100%;
            }
            .input:focus {
              outline: none;
            }
            
            &:focus-within {
              border: 1.5px solid v.$primario;
            }
        }
        .button-container{
            // display: flex;
            // justify-content: center;
            @include m.tablet{
                display: flex;
                flex-direction: row-reverse;
                gap: 2rem;
                max-width: 50rem;
            }
            .button-submit1 {
                margin: 20px 0 10px 0;
                background-color: v.$secundario;
                border: none;
                color: v.$blanco;
                font-weight: bold;
                font-size: 15px;
                font-weight: 500;
                border-radius: 10px;
                height: 50px;
                width: 100%;
                cursor: pointer;
                transition: v.$tran-btn;
                @include m.tablet{
                    width: 50%;
                }
                &:hover {
                  background-color: v.$secundario-hover;
                }
            }
            .button-submit2 {
                margin: 20px 0 10px 0;
                background-color: v.$primario;
                border: none;
                color: v.$negro;
                font-weight: bold;
                font-size: 15px;
                font-weight: 500;
                border-radius: 10px;
                height: 50px;
                width: 100%;
                cursor: pointer;
                transition: v.$tran-btn;
                @include m.tablet{
                    width: 50%;
                }
                &:hover {
                  background-color: v.$primario-hover;
                }
            }
        }
        .form__cargo-container{
            display: flex;
            justify-content: space-between;
            margin: 2rem 0;
            @include m.tablet{
                max-width: 40rem;
            }
            .form__puesto{
                font-size: 1.6rem;
            }
            .checkbox-wrapper-1{
                input[type="checkbox"]{
                    display: none;
                    visibility: hidden;
                }
                .cbx{
                    margin: auto;
                    -webkit-user-select: none;
                    user-select: none;
                    cursor: pointer;
                    span{
                        display: inline-block;
                        vertical-align: middle;
                        transform: translate3d(0, 0, 0);
                        &:first-child{
                            position: relative;
                            width: 18px;
                            height: 18px;
                            border-radius: 3px;
                            transform: scale(1);
                            vertical-align: middle;
                            border: 1px solid #9098a9;
                            transition: all 0.2s ease;
                            svg{
                                position: absolute;
                                top: 3px;
                                left: 2px;
                                fill: none;
                                stroke: #ffffff;
                                stroke-width: 2;
                                stroke-linecap: round;
                                stroke-linejoin: round;
                                stroke-dasharray: 16px;
                                stroke-dashoffset: 16px;
                                transition: all 0.3s ease;
                                transition-delay: 0.1s;
                                transform: translate3d(0, 0, 0);
                            }
                            &:before{
                                content: "";
                                width: 100%;
                                height: 100%;
                                background: v.$secundario;
                                display: block;
                                transform: scale(0);
                                opacity: 1;
                                border-radius: 50%;
                            }
                        }
                        &:last-child{
                            padding-left: 8px;
                        }
                        &:first-child{
                            border-color: v.$secundario;
                        }
                    }
                }
                .inp-cbx:checked + .cbx span:first-child{
                    background: v.$secundario;
                    border-color: v.$secundario;
                    animation: wave-46 0.4s ease;
                    svg{
                        stroke-dashoffset: 0;
                    }
                    &:before{
                        transform: scale(3.5);
                        opacity: 0;
                        transition: all 0.6s ease;
                    }
                }
            } 
            .checkbox-wrapper-2{
                input[type="checkbox"]{
                    display: none;
                    visibility: hidden;
                }
                .cbx{
                    margin: auto;
                    -webkit-user-select: none;
                    user-select: none;
                    cursor: pointer;
                    span{
                        display: inline-block;
                        vertical-align: middle;
                        transform: translate3d(0, 0, 0);
                        &:first-child{
                            position: relative;
                            width: 18px;
                            height: 18px;
                            border-radius: 3px;
                            transform: scale(1);
                            vertical-align: middle;
                            border: 1px solid #9098a9;
                            transition: all 0.2s ease;
                            svg{
                                position: absolute;
                                top: 3px;
                                left: 2px;
                                fill: none;
                                stroke: #ffffff;
                                stroke-width: 2;
                                stroke-linecap: round;
                                stroke-linejoin: round;
                                stroke-dasharray: 16px;
                                stroke-dashoffset: 16px;
                                transition: all 0.3s ease;
                                transition-delay: 0.1s;
                                transform: translate3d(0, 0, 0);
                            }
                            &:before{
                                content: "";
                                width: 100%;
                                height: 100%;
                                background: v.$secundario;
                                display: block;
                                transform: scale(0);
                                opacity: 1;
                                border-radius: 50%;
                            }
                        }
                        &:last-child{
                            padding-left: 8px;
                        }
                        &:first-child{
                            border-color: v.$secundario;
                        }
                    }
                }
                .inp-cbx:checked + .cbx span:first-child{
                    background: v.$secundario;
                    border-color: v.$secundario;
                    animation: wave-46 0.4s ease;
                    svg{
                        stroke-dashoffset: 0;
                    }
                    &:before{
                        transform: scale(3.5);
                        opacity: 0;
                        transition: all 0.6s ease;
                    }
                }
            }
            @keyframes wave-46 {
            50% {
                transform: scale(0.9);
            }
            }
              
        }
        .nota-password{
            text-align: center;
            @include m.tablet{
                max-width: 50rem;
                text-align: left;
            }
            p{
                font-size: 1.8rem;
                @include m.tablet{
                    margin-bottom: 0;
                }
            }
        }
    }
}