@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.navbar {
    background-color: v.$secundario;
    padding: 1rem 1rem;

    .nav-link,
    .navbar-brand,
    .dropdown-item {
        font-size: 2rem;
        color: v.$blanco;
    }

    .nav-link.dropdown-toggle {
        color: v.$blanco; 

        &:hover,
        &:focus {
            color: v.$blanco; 
        }
    }

    .dropdown-menu {
        background-color: v.$secundario;
        border-radius: 0.5rem;
        padding: 1rem;
    }

    .dropdown-item {
        color: v.$blanco;
        padding: 1rem;
        border-radius: 0.5rem;

        &:hover,
        &:focus {
            background-color: v.$blanco;
            color: v.$secundario;
        }
    }
    @media (max-width: 768px) {
        .nav-link,
        .navbar-brand,
        .dropdown-item {
            font-size: 1.5rem; 
        }
    }

    .navbar-brand img {
        display: none;
        height: 60px;
        width: 40px;
    
        @media (max-width: 768px) {
            display: block;
        }
    }
}