//Fuentes
$titulos: 'Montserrat', sans-serif;
$subtitulos: 'Roboto', sans-serif;
$texto: 'Dosis', sans-serif;

//Media Queries
$phone: 480px;
$tablet: 768px;
$desktop: 1024px;

//Colores
$primario: #F0F811;
$secundario: #6E12F8;
$bg-color: #F4EFFC;
$blanco: #fefeff;
$negro: #0c0c0c;
$gris: #666666;

//Hover
$primario-hover: #f3e51d;
$secundario-hover: #4d0bb1;
$bg-color-hover: #e1dee6;
$table-hover: #97989a;
$blanco-hover: #e9e9e9;

//Unidades
$separacion: 5rem;
$titulos-size: 4rem;
$subtitulos-size: 3.2rem;
$texto-size: 2rem;
$boton-size: 1.5rem;

//Transiciones
$tran-btn: all 0.2s ease;
$tran-text: all 0.1s ease;
$tran-bar: all 0.5s ease;