@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.recuperar{
    margin-left: 0;
    display: flex;
    flex-direction: column;
    height: 100%;
    @include m.tablet{
        flex-direction: row;
        height: 100vh;
        min-height: 76.8rem;
    }
    .recuperarHero{
        background-color: v.$secundario;
        min-height: 30rem;
        @include m.tablet{
            display: flex;
            min-height: 0;
            width: 40%;
            flex-direction: column;
            justify-content: center;
        }
        .recuperarHero__titulo{
            color: v.$blanco;
            font-weight: bold;
            text-transform: uppercase;
            text-align: center;
            margin: 3rem 0;
            font-size: 3.2rem;
            @include m.tablet{
                display: none;
            }
        }
        .recuperarHero__imagen{
            max-width: 16rem;
            margin: 0 auto 4rem auto;
        }
    }
    .recuperarInfo{
        text-align: center;
        padding: 2rem 4rem 0 4rem;
        @include m.tablet{
            text-align: left;
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 60%;
        }
        .recuperarInfo__titulo{
            display: none;
            @include m.tablet{
                display: inline-block;
            }
        }
        p{
            margin-bottom: 4rem;
        }
        form{
            .recuperarForm__email{
                font-size: 1.6rem;
            }
            .inputForm {
                border: 1.5px solid #ecedec;
                border-radius: 10px;
                height: 50px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-left: 10px;
                transition: 0.2s ease-in-out;
                margin-bottom: 2rem;
                font-size: 1.4rem;
                @include m.tablet{
                    max-width: 50rem;
                }
                .view-icon{
                    cursor: pointer;
                    margin-right: 2rem;
                }
                .input {
                  margin-left: 10px;
                  border-radius: 10px;
                  border: none;
                  width: 85%;
                  height: 100%;
                }
                .input:focus {
                  outline: none;
                }
                
                &:focus-within {
                  border: 1.5px solid v.$secundario;
                }
            }
        }
    }
    .button-container{
        // display: flex;
        // justify-content: center;
        @include m.tablet{
            display: flex;
            flex-direction: row-reverse;
            gap: 2rem;
            max-width: 50rem;
        }
        .button-submit1 {
            margin: 20px 0 10px 0;
            background-color: v.$primario;
            border: none;
            color: v.$negro;
            font-weight: bold;
            font-size: 15px;
            font-weight: 500;
            border-radius: 10px;
            height: 50px;
            width: 100%;
            cursor: pointer;
            transition: v.$tran-btn;
            @include m.tablet{
                width: 50%;
            }
            &:hover {
              background-color: v.$primario-hover;
            }
        }
        .button-submit2 {
            margin: 20px 0 10px 0;
            background-color: v.$secundario;
            border: none;
            color: v.$blanco;
            font-weight: bold;
            font-size: 15px;
            font-weight: 500;
            border-radius: 10px;
            height: 50px;
            width: 100%;
            cursor: pointer;
            transition: v.$tran-btn;
            @include m.tablet{
                width: 50%;
            }
            &:hover {
              background-color: v.$secundario-hover;
            }
        }
    }
}