@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.cambiarPassword{
    display: flex;
    flex-direction: column;
    margin-left: 0;
    height: 100%;
    @include m.tablet{
        flex-direction: row;
        height: 100vh;
        min-height: 76.8rem;
    }
    .cambiarHero{
        background-color: v.$secundario;
        min-height: 30rem;
        @include m.tablet{
            display: flex;
            min-height: 0;
            width: 40%;
            flex-direction: column;
            justify-content: center;
        }
        .cambiarHero__titulo{
            color: v.$blanco;
            font-weight: bold;
            text-transform: uppercase;
            text-align: center;
            margin: 3rem 0;
            font-size: 3.2rem;
            @include m.tablet{
                display: none;
            }
        }
        .cambiarHero__imagen{
            max-width: 16rem;
            margin: 0 auto 4rem auto;
        }
    }
    .cambiarForm{
        text-align: left;
        background-color: v.$blanco;
        padding: 2rem 4rem 0 4rem;
        p{
            text-align: center;
            @include m.tablet{
                text-align: left;
            }
        }
        @include m.tablet{
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 60%;
        }
        .cambiarForm__titulo{
            display: none;
            @include m.tablet{
                display: inline-block;
                margin-bottom: 4rem;
                font-size: 3rem;
            }
        }
        .cambiarForm__password{
            font-size: 1.6rem;
        }  
        .inputForm {
            border: 1.5px solid #ecedec;
            border-radius: 10px;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-left: 10px;
            transition: 0.2s ease-in-out;
            margin-bottom: 2rem;
            font-size: 1.4rem;
            @include m.tablet{
                max-width: 50rem;
            }
            .view-icon{
                cursor: pointer;
                margin-right: 2rem;
            }
            .input {
                margin-left: 10px;
                border-radius: 10px;
                border: none;
                width: 85%;
                height: 100%;
            }
            .input:focus {
                outline: none;
            }
            
            &:focus-within {
                border: 1.5px solid v.$secundario;
            }
        }          
        .button-container{
            // display: flex;
            // justify-content: center;
            margin-bottom: 4rem;
            .button-submit {
                margin: 20px 0 10px 0;
                background-color: v.$primario;
                border: none;
                color: v.$negro;
                font-weight: bold;
                font-size: 15px;
                font-weight: 500;
                border-radius: 10px;
                height: 50px;
                width: 100%;
                cursor: pointer;
                transition: v.$tran-btn;
                @include m.tablet{
                    width: 50%;
                }
                &:hover {
                  background-color: v.$primario-hover;
                }
            }
        }
          
    }
}