@use 'Base/variables' as v;

.title-example{
    font: v.$titulos;
    margin: 0;
    font-size: v.$titulos-size;
}
.subtitle-example{
    font: v.$subtitulos;
    margin: 0;
    font-size: v.$subtitulos-size;
}
.paragraph-example{
    font: v.$texto;
    margin: 2rem 0 0 0;
    font-size: v.$texto-size;
}